
.container {
	margin-right: auto;
	margin-left: auto;
	padding-left: 15px;
	padding-right: 15px;
	@include r(319) {
		width: 200px; }
	@include rmin(320) {
		width: 80%; }
	@include rmin(480) {
		width: 90%; }
	@include rmin(768) {
		width: 100%; }
	@include rmin(1024) {
		width: 90%; }
	@include rmin(1200) {
		width: 80%; }
	@include rmin(1600) {
		width: 1400px; } }

a {
    color: #000;
    text-decoration: none; }

.hero {
	height: 70px;
	@include r(480) {
		height: 50px; }
	box-shadow: 0 2px 9px rgba(189, 189, 189, 0.5); }
@include rmin(480) {
	.top_menu {
		input {
			display: none; } } }
.logo {
	position: relative;
	height: 31px;
	img {
		position: absolute;
		top: 70%;
		@include r(480) {
			top: 40%;
			left: -16%; } } }
@include r(320) {
	#menuToggle {
		left: -35px; } }
@include r(480) {
	#menuToggle {
		display: block;
		position: relative;
		z-index: 1;
		-webkit-user-select: none;
		user-select: none;
		width: 40px;
		height: 32px;
		top: 60px;
		left: -40px; }
	#menuToggle {
		input {
			display: block;
			width: 40px;
			height: 32px;
			position: absolute;
			top: -7px;
			left: -5px;
			cursor: pointer;
			opacity: 0;
			z-index: 2;
			-webkit-touch-callout: none; } }
	#menuToggle {
		span {
			display: block;
			width: 33px;
			height: 4px;
			margin-bottom: 5px;
			position: relative;
			background: #cdcdcd;
			border-radius: 3px;
			z-index: 1;
			transform-origin: 4px 0px;
			transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0); } }
	#menuToggle {
		span:first-child {
			transform-origin: 0% 0%; } }

	#menuToggle {
		span:nth-last-child(2) {
			transform-origin: 0% 100%; } }
	#menuToggle {
		input:checked {
			&~span:nth-last-child(3) {
				opacity: 0 {
				 transform: rotate(0deg) scale(0.2, 0.2); } } } }
	#menuToggle {
		input:checked {
			&~span:nth-last-child(2) {
				transform: rotate(-45deg) translate(0, -1px); } } }
	#menuToggle {
		input:checked {
			&~span {
				transform: rotate(45deg) translate(0, -1px); } } }
	#menu {
		position: absolute;
		width: 360px;
		margin: -70px 0 0 -50px;
		padding-top: 60px;
		padding-bottom: 40px;
		background: #ededed;
		list-style-type: none;
		-webkit-font-smoothing: antialiased;
		transform-origin: 0% 0%;
		transform: translate(-100%, 0);
		transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0); }
	#menu {
		li {
			padding: 10px 110px;
			font-size: 22px;
			float: none; } }
	#menuToggle input:checked {
		&~ul {
			transform: none; } } }

#menu {
	@include flex;
	@include r(480) {
		display: block; }
	li {
		padding-right: 60px;
		text-transform: uppercase;
		font-size: 11px;
		@include font-exter(PT Sans Narrow, 400);
		@include r(768) {
			padding-right: 18px; }
		&:first-child {
			margin-left: 90px;
			@include r(768) {
				margin-left: 45px; }
			@include r(480) {
				margin-left: 0; } }
		a {
			color: #9297a3;
			&:active {
				font-weight: 700;
				color: #000; }
			&:hover {
				text-decoration: underline; } } } }

.top_header {
	position: relative; }
.user_interface {
	position: absolute;
	top: 0%;
	right: 0%;
	@include r(767) {
		right: -5%; }
	@include r(1400) {
		right: 0%; } }

.user_interface__cart {
	@include flex; }
.cart {
	border-right: 1px solid #dedede;
	height: 70px;
	@include r(480) {
		height: 50px; }
	@include flex;
	align-items: center; }

.cart__bag {
	@include fs(13);
	@include font_exter(PT Sans Narrow, 700);
	padding-right: 15px;
	@include r(768) {
		padding-right: 10px; } }

.cart__number {
	height: 15px;
	width: 15px;
	background-color: #000;
	border-radius: 50%;
	margin-right: 40px;
	@include r(768) {
		margin-right: 10px; }
	a {
		color: #fff;
		@include fs(11);
		display: block;
		margin-top: -2px;
		text-align: center;
		@include font_exter(Georgia Regular, 400); } }

.sign_in {
	@include flex;
	@include fs(13);
	@include font_exter(PT Sans Narrow, 700);
	align-items: center;
	padding-left: 40px;
	justify-content: flex-end;
	div {
		padding: 10px; }
	@include r(768) {
		padding-left: 5px; } }


// Container

.item_container {
	@include flex;
	padding-top: 20px;
	@include r(480) {
		flex-direction: column;
		margin-right: 15px; }
	@include rmin(1200) {
		margin-left: 85px;
		margin-top: 25px; } }

.item_slider {
	width: 8%;
	@include r(480) {
		order: 3;
		width: 100%;
		@include flex;
		justify-content: space-around;
		margin-left: 15px; } }

.item_image {
	padding-left: 15px;
	width: 35%;
	@include r(480) {
		order: 1;
		width: 70%;
		margin-left: 40px; } }

.item_info {
	width: 50%;
	@include flex;
	flex-direction: column;
	text-align: center;
	margin-left: 32px;
	@include r(480) {
		width: 90%;
		order: 2; }
	.btn_buy {
		margin-left: 55px;
		margin-top: 75px;
		@include r(480) {
			margin: 60px auto; } }
	&>h3 {
		text-transform: uppercase;
		@include fs(23);
		@include font-exter(PT Sans Narrow, 700);
		margin-top: 10px;
		@include r(768) {
			@include fs(18); }
		@include r(320) {
			@include fs(15); } } }

.item__category {
	@include fs(13);
	@include font-exter(Georgia, 400);
	margin-top: 20px;
	a, span {
		color: #9fcede; } }

.item__price {
	@include fs(22);
	@include font_exter(Georgia, 400);
	margin-top: 50px;
	margin-left: 10px;
	@include r(768) {
		@include fs(18); }
	@include r(480) {
		margin-top: 20px; } }

.item__description {
	&>h4 {
		@include fs(13);
		@include font_exter(PT Sans Narrow, 700);
		margin-top: 50px;
		margin-left: 10px; }

	&>p {
		@include fs(12);
		@include font-exter(Georgia, 400);
		color: #9297a3;
		width: 450px;
		padding-top: 20px;
		margin: 5px auto;
		border-top: 1px #dbdee5 solid;
		@include r(1200) {
			width: 400px; }
		@include r(980) {
			width: 350px; }
		@include r(768) {
			width: 230px;
			line-height: 1.64; }
		@include r(480) {
			width: 200px; }
		@include r(320) {
			width: 190px; } } }

.color_line {
	label {
		margin-top: 35px;
		&>input[type=radio] {
			display: none;
			&:checked {
				&+span {
					background: #000; } } } } }
.color_line {
	span {
		background: #fff;
		border: solid 1px #bbb;
		display: inline-block;
		width: 10px;
		height: 10px;
		border-radius: 100%;
		vertical-align: top; } }

.colors {
	@include r(480) {
		// margin-top: 150px
		margin-bottom: 15px; }
	p {
		@include fs(13);
		padding-top: 5px; }
	&>p:first-of-type {
		color: #000;
		@include font_exter(PT Sans Narrow, 900);
		text-align: left;
		padding-left: 75px;
		padding-top: 70px;
		@include r(480) {
			padding-top: 20px;
			text-align: center;
			padding-left: 0; } }

	hr {
		width: 145px;
		margin: 10px 0 0 70px;
		display: block;
		border: 0;
		border-top: 1px solid #dbdee5;
		@include r(480) {
			width: 100px;
			margin-left: 50px; } }
	.color_line {
		@include flex;
		flex-direction: column;
		margin-top: -22px; } }


.this_color {
	width: 10px;
	height: 10px;
	margin-top: 35px;
	margin-left: 10px; }


.color__each {
	@include flex;
	@include font_exter(PT Sans Narrow, 700);
	height: 40px;
	padding-left: 50px;
	&>p {
		margin-top: 25px;
		margin-left: 10px; } }

.btn_buy {
	width: 489px;
	height: 49px;
	border-radius: 2px;
	background-color: #afa394;
	color: #ffffff;
	@include font_exter(Georgia, 400);
	@include fs(13);
	margin-top: 10px;
	margin-bottom: 115px;
	&:hover {
		background-color: #afafaf; }
	&:active {
		box-shadow: 0 8px 10px #d0d0d0; }
	@include r(1200) {
		width: 350px; }
	@include r(980) {
		width: 300px; }
	@include r(768) {
		width: 180px; }
	@include r(480) {
		margin-left: 0; } }


// Item offer slider

.offer_header {
	margin: 0 auto;
	text-align: center;
	&>h6 {
		color: #9297a3;
		@include fs(12);
		@include font-exter(Georgia, 400); }
	&>h5 {
		text-transform: uppercase;
		@include fs(16);
		@include font_exter(PT Sans Narrow, 600); } }
.offer_slider {
	margin-top: 20px;
	@include flex;
	justify-content: space-evenly;
	@include r(1200) {
		flex-wrap: wrap; } }

.offer-slider__item {
	padding-bottom: 25px;
	&>.offer-slider__image {
		background-color: #f5f5f5;
		@include flex;
		justify-content: center;
		height: 225px;
		width: 220px; } }

.item__information {
	position: relative;
	margin-top: 15px; }
.item__information--name {
	@include fs(16);
	@include font-exter(PT Sans Narrow, 600);
	text-transform: uppercase; }

.item__information--category {
	@include fs(11);
	@include font-exter(Georgia, 400);
	color: #9297a3; }

.item__information--price {
	@include fs(13);
	@include font-exter(Georgia, 400);
	color: #888b92;
	text-align: right;
	position: absolute;
	top: 0;
	right: 5%; }
